.filter-tag {
    padding: 4px 0;
    width: auto;
    display: inline-block;
    width: 160px;
    border-radius: 8px;
}

.tag-name {
    margin-left: 48px;
    width: 120px;
    line-height: 23px;
    color: #ccc;
    white-space: pre-line;
}

.switch {
    position: relative;
    display: inline-block;
    border-radius: 23px;
    width: 40px;
    height: 23px;
    cursor: pointer;
}

.switch:hover {
    -webkit-transition: -webkit-box-shadow 0.25s cubic-bezier(.25,.8,.25,1);
    transition: -webkit-box-shadow 0.25s cubic-bezier(.25,.8,.25,1);
    -o-transition: box-shadow 0.25s cubic-bezier(.25,.8,.25,1);
    transition: box-shadow 0.25s cubic-bezier(.25,.8,.25,1);
    transition: box-shadow 0.25s cubic-bezier(.25,.8,.25,1), -webkit-box-shadow 0.25s cubic-bezier(.25,.8,.25,1);
    -webkit-box-shadow: 0 0 8px green;
            box-shadow: 0 0 8px green;
}

.switch input {display:none;}

input:checked + .slider {
    background-color: green;
}

input:checked ~ .tag-name {
    color: green;
}

input:checked + .slider:before {
    -webkit-transform: translateX(16px);
        -ms-transform: translateX(16px);
            transform: translateX(16px);
}

.slider {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: .4s;
    -o-transition: .4s;
    transition: .4s;
    border-radius: 23px;
}

.slider:before {
    position: absolute;
    content: "";
    height: 16px;
    width: 16px;
    left: 3px;
    bottom: 3px;
    background-color: white;
    -webkit-transition: .4s;
    -o-transition: .4s;
    transition: .4s;
    border-radius: 50%;
}


