.project-image {
    display: inline-block;
    margin: 18px 24px 6px 0;
    width: 520px;
    height: 460px;
    border-radius: 8px;
    overflow: hidden;
    position: relative;
    -webkit-box-shadow: 0 0 6px rgba(0,0,0,0.12), 0 0 6px rgba(0,0,0,0.24);
            box-shadow: 0 0 6px rgba(0,0,0,0.12), 0 0 6px rgba(0,0,0,0.24);
    -webkit-transition: all 0.35s cubic-bezier(.25,.8,.25,1);
    -o-transition: all 0.35s cubic-bezier(.25,.8,.25,1);
    transition: all 0.35s cubic-bezier(.25,.8,.25,1);
    cursor: pointer;
}

.project-image:hover {
    -webkit-box-shadow: 0 0 12px rgba(0,0,0,0.12), 0 0 12px rgba(0,0,0,0.24);
            box-shadow: 0 0 12px rgba(0,0,0,0.12), 0 0 12px rgba(0,0,0,0.24);
}

.project-image img {
    position: absolute;
    left: 50%;
    top: 50%;
    height: 100%;
    width: auto;
    -webkit-transform: translate(-50%,-50%);
        -ms-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
}

@media screen and (max-width: 600px) {
    .project-image {
        width: 100%;
        margin: 8px 0 8px 0;
    }
}


